.container {
  max-width: 100%;
  margin: 0 auto;
  padding: 0 20px;
  width: 1600px; }

.header {
  position: relative;
  padding: 48px 0 0; }

.header__row {
  display: flex;
  justify-content: space-between; }

.header__logo:hover, .header__logo:focus, .header__logo:active {
  cursor: pointer;
  text-decoration: underline;
  opacity: 0.8; }

.header__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap; }

.header__item {
  margin-right: auto; }
  .header__item:first-child .header__link {
    padding-left: 0; }

.header__link {
  display: block;
  padding: 15px;
  text-decoration: none;
  text-transform: uppercase;
  color: #ff6078; }
  .header__link:hover, .header__link:focus, .header__link:active {
    cursor: pointer;
    text-decoration: underline;
    opacity: 0.8; }

.header__button {
  margin-top: 8px;
  margin-right: 4px;
  display: block;
  padding: 0;
  border: none;
  background-color: transparent;
  outline: none; }

.header__nav {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  transition: all 0.2s ease-in;
  transform-origin: 0 0;
  opacity: 1;
  background-color: #ffffff; }
  .header__nav--inactive {
    transform-origin: 0 0;
    transform: scaleY(0);
    opacity: 0;
    transition: all 0.2s ease-in; }

@media (max-width: 1280px) {
  .header__item:first-child .header__link {
    padding-left: 15px; } }

@media (max-width: 767px) {
  .header__list {
    display: block; }
  .header__item {
    text-align: center; }
  .header__link {
    padding-bottom: 0; }
  .header__nav {
    padding-bottom: 15px; } }

.header-top__row {
  display: flex;
  flex-wrap: wrap; }

.header-top__logo:hover, .header-top__logo:focus, .header-top__logo:active {
  cursor: pointer;
  text-decoration: underline;
  opacity: 0.8; }

.header-top__text {
  max-width: 170px;
  margin: 0;
  font-family: "MuseoSansCyrl", "Arial", sans-serif;
  font-weight: 300;
  line-height: 1;
  font-size: 14px;
  color: #000000; }

.header-top__adress {
  margin: 0;
  font-family: "MuseoSansCyrl", "Arial", sans-serif;
  font-weight: 500;
  line-height: 1;
  font-size: 16px;
  color: #000000; }

.header-top__map-link {
  font-family: "MuseoSansCyrl", "Arial", sans-serif;
  font-weight: 300;
  line-height: 1;
  font-size: 12px;
  color: #ff6078;
  text-transform: uppercase;
  text-decoration: dashed; }

.header-top__phone {
  font-family: "MuseoSansCyrl", "Arial", sans-serif;
  font-size: 24px;
  line-height: 1.5;
  color: #000000;
  text-decoration: none; }
  .header-top__phone span {
    display: block;
    position: relative;
    font-size: 12px; }
    .header-top__phone span::after {
      content: "";
      position: absolute;
      left: -10px;
      top: 50%;
      transform: translateY(-50%);
      display: block;
      width: 8px;
      height: 8px;
      background-color: #27b24b;
      border-radius: 50%;
      box-shadow: 0px 0px 16px rgba(248, 248, 248, 0.3); }

.header-top__img {
  width: 35px;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
  border-radius: 50%;
  box-shadow: 0px 0px 16px rgba(248, 248, 248, 0.3); }

.header__top-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 230px;
  height: 60px;
  font-family: "MuseoSansCyrl", "Arial", sans-serif;
  font-size: 15px;
  line-height: 1.2;
  color: #000000;
  text-decoration: none;
  background-color: #ffffff;
  border-radius: 25px;
  box-shadow: 0px 3px 1px 0px #ebeaea; }
